    additional:{
        footerScripts:props.segment.additional && props.segment.additional.footerScripts ? props.segment.additional.footerScripts : ``,
        headerScripts:props.segment.additional && props.segment.additional.headerScripts ? props.segment.additional.headerScripts : ``,
        hideHeader: props.segment.additional && props.segment.additional.hideHeader ? props.segment.additional.hideHeader : false,
        customTitle: props.segment.additional && props.segment.additional.customTitle ? props.segment.additional.customTitle  : false,
        customTitleText: props.segment.additional && props.segment.additional.customTitleText ? props.segment.additional.customTitleText : null,
        subTitle: props.segment.additional && props.segment.additional.subTitle ? props.segment.additional.subTitle : null,
        filters:props.segment.additional && props.segment.additional.filters ? props.segment.additional.filters : {
            sube:{
