                label:"KATEGORLER"
            },
            price:{
                active:false,
                label:"CRET"
            },
            attributes:{
                active:false,
                label:"ZELLKLER"
            },
            event_date:{
                active:false,
                label:"ETKNLK TARH"
            }
        },
    },
    media_objects:props.segment.media_objects ? props.segment.media_objects : {
        mainImage:null,
        headerImage:null,
        mainVideo:null,
        galleryImages:[],
    },
    seo:{
        title:props.segment.seo ? props.segment.seo.title : "",
        description:props.segment.seo ? props.segment.seo.description : ""
